<script>
  import { orders, orderState } from "../store/data.js";

  const statusFilter = ["completed", "cancelled", "refunded", "failed"];
  let emails;
  $: emails = $orders.orders
    .map(o => ({ state: $orderState[o.id], order: o }))
    .filter(o => !o.state.done)
    .filter(o => !statusFilter.includes(o.order.status))
    .map(o => o.order.billing.email);
  $: emailLink = `mailto:${emails.join(",")}`;
</script>

<a class="button" href={emailLink}>Email to all Not Done</a>